.backdrop__reg-modal {
  height: 100vh;
  width: 100vw;
  z-index: 2;
  background: rgba(0, 0, 0, .8);
  transition: opacity .25s, visibility .25s;
  position: fixed;
  top: 0;
  left: 0;
  overflow-x: hidden;
  overflow-y: auto;
}

.backdrop__reg-modal.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal-reg {
  width: 320px;
  height: 500px;
  background: var(--light-color);
  border-radius: 4px;
  padding: 30px;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: hidden;
  transform: translate(-50%, -50%);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

